@import "/theme/variables/colors.scss";
.dropdown {
    position: relative;
    user-select: none;
    width: 320px;

    &__head {
        display: flex;
        align-items: center;
        border: 1px solid $dark-shade-25;
        border-radius: 4px 4px 0 0;
        cursor: pointer;
        height: 44px;
        width: inherit;
        box-sizing: border-box;
    }
    &__text {
        padding-left: 15px;
        padding-right: 47px;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        width: 100%;
        box-sizing: border-box;
    }
    &__icon {
        width: 44px;
        text-align: center;
    }
    &__list {
        position: absolute;
        background: #fff;
        width: inherit;
        box-sizing: border-box;
        z-index: 1;
        border: 1px solid $dark-shade-25;
        border-top: none;
        border-radius: 0 0 4px 4px;
        padding: 7px 7px 13px 15px;
        display: none;
        &.show {
            display: block;
        }
    }

    &__item {
        min-width: 100%;
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 200px;
        box-sizing: border-box;

        // padding: 12px 7px 10px 15px;
        margin-bottom: 7px;
        span {
            font-style: normal;
            font-weight: bold;
            font-size: 12px;
            line-height: 15px;

            text-transform: uppercase;

            color: $dark-shade-100;
        }
    }

    &__switcher {
        width: 92px;
        height: 100%;
        padding: 0px;
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        justify-content: space-between;
        padding: 0px !important;
    }

    &__minus,
    &__plus {
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 18px;
        line-height: 22px;
        border: 1px solid $dark-shade-25;
        border-radius: 22px;
        width: 30px;
        height: 30px;
        padding: 0px !important;
        cursor: pointer;
    }
    &__minus {
        opacity: 0.5;
    }

    &__number {
        align-items: center;
        justify-content: center;
        padding: 0px !important;
    }

    &__toolbar {
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 100%;
        margin-top: 12px;
        box-sizing: border-box;
        > div {
            cursor: pointer;
        }
    }

    &__clear {
        visibility: hidden;
        h3 {
            color: $dark-shade-50;
        }
    }

    &__apply h3 {
        color: $purple;
    }
}
